.movieCard {
  display: flex;
}
.cardText {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 15px;
  font-weight: 400;
}
.genresList {
  display: flex;
  justify-items: flex-start;
  font-size: 15px;
  font-weight: 700;
}
img {
  border-radius: 3px;
  margin-bottom: 10px;
}
.genreItem {
  margin-right: 5px;
}
.reviewList {
  display: flex;
  font-size: 20px;
  padding-bottom: 10px;
}
.navItem {
  margin-right: 30px;
  list-style: none;
}
